import request from '@/utils/request'

export function loginByCode(phone,verify) {
  const data={
    phone,
    verify,
    type:2
  }
  return request({
    url:'/user/login',
    method:'post',
    data
  })
}

export function Resetpw(password) {
  const data={
    password
  }
  return request({
    url:'/user/resetpw',
    method:'post',
    data
  })
}

export function loginByPsw(phone,password) {
  const data={
    phone,
    password,
    type:1
  }
  return request({
    url:'/user/login',
    method:'post',
    data
  })
}
export function getUserInfo() {
  return request({
    url:'/user/personinfo',
    method:'post',
  })
}

export function sendCode(phone,type) {
  const data={
    phone,
    type
  }
  return request({
    url:'/user/sendsms',
    method:'post',
    data
  })
}
export function signUp(data) {
  return request({
    url:'/user/register',
    method:'post',
    data
  })
}

export function logOut() {
  return request({
    url:'/user/logout',
    method:'post',
  })
}
