<template>
	<div>
		<van-nav-bar class="hader indexhader" left-text="返回" left-arrow>
			<router-link to="/" slot="left"><div class="logo"></div></router-link>
			<a href="https://m.guanfan.top/#/app" slot="right" v-if="isiOS != true && isWeixin != true"><van-button type="primary" size="small">安装APP</van-button></a>
			<a href="https://apps.apple.com/cn/app/id1487127641" slot="right" v-if="isiOS == true && isWeixin != true">
				<van-button type="primary" size="small">安装APP</van-button>
			</a>
			<a href="https://mp.weixin.qq.com/mp/profile_ext?action=home&__biz=MzU0NTg5ODQzNA==&scene=124#wechat_redirect" slot="right" v-if="isWeixin == true">
				<van-button type="primary" size="small">关注</van-button>
			</a>
		</van-nav-bar>
		<div class="cont">
			<van-search class="mb10" v-model="value" placeholder="请输入搜索关键词" show-action focus shape="round">
				<div slot="action" @click="Search(value)">搜索</div>
			</van-search>
			<h2 class="text-18 tit mb10">
				<span class="icon icon-hot"></span>
				热门搜索
			</h2>
			<div class="hot-txet">
				<ul>
					<li v-for="(item, index) in hottext" :key="index" @click="Search(item)">{{ item }}</li>
				</ul>
			</div>

			<div class="muen-img mt10">
				<router-link class="left" to="/themelist"></router-link>
				<router-link class="right" to="/sort"></router-link>
			</div>

			<h2 class="text-18 tit mb10">
				<span class="icon icon-jx"></span>
				精选食谱
			</h2>
			<LoadMore :onLoadMore="onLoadMore" :enableLoadMore="enableLoadMore">
				<div class="food-list1">
					<!-- <mu-load-more @refresh="refresh" :refreshing="refreshing" :loading="loading" @load="load"> -->
					<ul>
						<li v-for="(item, index) in foodlist" :key="index" @click="bookDetail(item.cpid)">
							<div class="foodlist-img" :style="{ backgroundImage: 'url(' + osshost + item.cpimg + listpic + ')' }"></div>
							<div class="food-list1-tit text-cut">{{ item.cptitle }}</div>
							<div class="user">
								<img :src="imgurl" v-if="!item.faceimg">
								<img :src="osshost + item.faceimg +userface" v-if="item.faceimg&&item.isThree==false">
								<img :src="item.faceimg" v-if="item.faceimg&&item.isThree==true">
								<span>{{ item.nickname }}</span>
								<van-icon class="icon-like2" name="like">{{ item.collections }}</van-icon>
							</div>
						</li>
					</ul>
					<!-- </mu-load-more> -->
				</div>
			</LoadMore>
		</div>
		<Totop></Totop>
	</div>
</template>

<script>
import qs from 'qs';
import axios from 'axios';

import LoadMore from '../components/LoadMore.vue';
import Totop from '../components/totop.vue';
//import { getCateRecipe } from '../api/main.js';
export default {
	components: {
		LoadMore,
		Totop
	},
	data() {
		return {
			labelPosition: 'top',
			listpic: '?x-oss-process=style/list-img',
			userface: '?x-oss-process=style/user-img',
			detailpic: '?x-oss-process=style/detail-img',
			osshost: 'http://guanfan.oss-cn-hangzhou.aliyuncs.com/',
			// osshost: 'http://guanfan.oss-cn-hangzhou.aliyuncs.com/',
			hottext: [],
			foodlist: [],
			enableLoadMore: true,
			page: 1,
			value: '',
			isWeixin: '',
			isiOS: '',
			imgurl: '../assets/img/userIcon.png',
			isThree: false
		}
	},
	created() {
		this.getMenu();
		this.getHot();
		this.getwx();
		this.appSource();
	},
	methods: {
		getwx() {
			var ua = window.navigator.userAgent.toLowerCase();
			if (ua.match(/MicroMessenger/i) == 'micromessenger') {
				this.isWeixin = true;
			} else {
				this.isWeixin = false;
			}
		},
		appSource() {
			const u = navigator.userAgent,
				app = navigator.appVersion;
			if (!!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/)) {
				this.isiOS = true;
			} else {
				this.isiOS = false;
			}
		},
		onLoadMore(done) {
			setTimeout(() => {
				if (!this.enableLoadMore) {
					return;
				}
				this.page = this.page + 1;
				this.getMenu();
				done();
			}, 500);
		},
		Search(key) {
			this.$router.push({ path: '/search', query: { key: key } });
		},
		getMenu() {
			let data = qs.stringify({
				page: this.page
			});
			axios({
				method: 'post',
				url: '/index/featuredrecipe',
				headers: {
					'Content-Type': 'application/x-www-form-urlencoded'
				},
				data
			}).then(response => {
				if (response.data.success) {
					if (response.data.result.length < 10) {
						this.enableLoadMore = false;
					}
					this.foodlist = this.foodlist.concat(response.data.result);
					for(var i=0;i<this.foodlist.length;i++){
						this.foodlist[i].isThree = new RegExp('http').test(this.foodlist[i].faceimg);
					};
				}
			});
		},
		getHot() {
			axios({
				method: 'post',
				url: '/search/hotword',
				headers: {
					'Content-Type': 'application/x-www-form-urlencoded'
				}
			}).then(response => {
				if (response.data.success) {
					this.hottext = response.data.result;
				}
			});
		},
		bookDetail(id) {
			this.$router.push({ path: '/cookbookinfo', query: { id: id } });
		}
	}
};
</script>

<style></style>
