import { loginByCode, loginByPsw,getUserInfo,logOut } from '@/api/login'
import { getToken, setToken, removeToken } from '@/utils/auth'
import { Message } from 'element-ui'
const user = {
  state: {
    token: getToken('web_token'),
    user_info:getToken('user_info'),
    isOpenBox:false
  },
  mutations: {
    SET_TOKEN: (state, token) => {
      state.token = token
    },
    SET_USER: (state, token) => {
      state.user_info = token
    },
    SET_OPENBOX: (state, isOpenBox) => {
      state.isOpenBox = isOpenBox
    }
  },
  actions: {
    LoginByCode({ commit }, userInfo) {
      const phone = userInfo.phone.trim()
      return new Promise((resolve, reject) => {
        loginByCode(phone, userInfo.verify).then(response => {
          const data = response.data
          if(data.success){
            commit('SET_TOKEN', data.result.token)
            setToken('web_token',data.result.token)
            resolve()
          }else{
            Message({
              message: data.message,
              duration: 5 * 1000
            })
          }
        }).catch(error => {
          reject(error)
        })
      })
    },
    // 验证码登录
    LoginByPsw({ commit }, userInfo) {
      const phone = userInfo.phone.trim()
      return new Promise((resolve, reject) => {
        loginByPsw(phone, userInfo.password).then(response => {
          const data = response.data
          if(data.success){
            commit('SET_TOKEN', data.result.token)
            setToken('web_token',data.result.token)
            resolve()
          }else{
            Message({
              message: data.message,
              duration: 5 * 1000
            })
          }
        }).catch(error => {
          reject(error)
        })
      })
    },
    //获取用户信息
    GetUserInfo({ commit, state }) {
      return new Promise((resolve, reject) => {
        getUserInfo(state.token).then(response => {
          const data = response.data
          if(data.success){
            commit('SET_USER', data.result)
            setToken('user_info',data.result)
          }else{
            Message({
              message: data.message,
              duration: 5 * 1000
            })
          }
          resolve(response)
        }).catch(error => {
          reject(error)
        })
      })
    },

    // 第三方验证登录
    // LoginByThirdparty({ commit, state }, code) {
    //   return new Promise((resolve, reject) => {
    //     commit('SET_CODE', code)
    //     loginByThirdparty(state.status, state.email, state.code).then(response => {
    //       commit('SET_TOKEN', response.data.token)
    //       setToken(response.data.token)
    //       resolve()
    //     }).catch(error => {
    //       reject(error)
    //     })
    //   })
    // },

    // 登出
    LogOut({ commit, state }) {
      return new Promise((resolve, reject) => {
        logOut().then((response) => {

          const data = response.data
          if(data.success){
            commit('SET_TOKEN', '')
            commit('SET_USER', '')
            removeToken('web_token')
            removeToken('user_info')
            resolve()
          }else{
            Message({
              message: data.message,
              duration: 5 * 1000
            })
          }
        }).catch(error => {
          reject(error)
        })
      })
    },

    // 前端 登出
    FedLogOut({ commit }) {
      return new Promise(resolve => {
        commit('SET_TOKEN', '')
        removeToken('web_token')
        removeToken('user_info')
        resolve()
      })
    },

    toggleBox({ commit }, status) {
      commit('SET_OPENBOX', status)
    },
  }
}

export default user
