import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/view/index'
import axios from 'axios'

Vue.use(Router)

// const routerPush = Router.prototype.push
// Router.prototype.push = function push(location) {
//   return routerPush.call(this, location).catch(error=> error)
// }

axios.defaults.baseURL = 'https://api.guanfan.top'


export default new Router({
	routes: [{
			path: '/',
			name: 'index',
			component: Index
		},
		{
			path: '/cookbookinfo',
			name: 'cookbookinfo',
			component: () => import('@/view/cookbookinfo'),
		},
		{
			path: '/app',
			name: 'app',
			component: () => import('@/view/app'),
		},
		{
			path: '/themeinfo',
			name: 'themeinfo',
			component: () => import('@/view/themeinfo'),
		},
		{
			path: '/themelist',
			name: 'themelist',
			component: () => import('@/view/themelist'),
		},
		{
			path: '/sort',
			name: 'sort',
			component: () => import('@/view/sort'),
		},
		{
			path: '/search',
			name: 'search',
			component: () => import('@/view/search'),
		},
		{
			path: '/dtinfo',
			name: 'dtinfo',
			component: () => import('@/view/dtinfo'),
		},
		{
			path: '/active',
			name: 'active',
			component: () => import('@/view/active'),
		},
		{
			path: '/codeLogin',
			name: 'codeLogin',
			component: () => import('@/view/codeLogin'),
		},
		{
			path: '/pswLogin',
			name: 'pswLogin',
			component: () => import('@/view/pswLogin'),
		},
		{
			path: '/reg',
			name: 'reg',
			component: () => import('@/view/reg'),
		},
	]
})
